
"use client";

import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { useTranslations } from "@/hooks/useTranslations";
import { Paintbrush, Sprout, Palette } from "lucide-react"; // Added Palette icon

interface PaletteInfoModalProps {
  isOpen: boolean;
  onClose: () => void;
}

export default function PaletteInfoModal({ isOpen, onClose }: PaletteInfoModalProps) {
  const t = useTranslations().paletteInfoModal || {
    title: "About the Visuals",
    closeButton: "Close",
    paletteTitle: "Color Palette",
    paletteDescription1: "The color palette of this portfolio is designed to be vibrant, modern, and adaptable, reflecting the dynamic nature of technology and creativity.",
    primaryColorLabel: "Primary Color",
    primaryColorDescription: "Used for key actions, highlights, and branding elements. Represents innovation and clarity.",
    secondaryColorLabel: "Secondary Color",
    secondaryColorDescription: "Complements the primary color, often used for secondary actions or visual accents. Evokes creativity and energy.",
    accentColorLabel: "Accent Color",
    accentColorDescription: "Provides contrast and draws attention to specific interactive elements or calls to action.",
    paletteDescription2: "These colors are thoughtfully applied throughout the UI and the 3D background to create a cohesive and engaging user experience, adapting gracefully between light and dark themes.",
    spiralTitle: "The 3D Spiral",
    spiralDescription1: "The animated 3D spiral in the background symbolizes continuous growth, perpetual learning, and the interconnectedness of ideas within the vast universe of technology.",
    spiralDescription2: "Its dynamic movement and shifting colors reflect the ever-evolving nature of the digital world and my personal journey of exploration and development within it.",
  };

  if (!isOpen) return null;

  const colorInfo = [
    { 
      label: t.primaryColorLabel, 
      description: t.primaryColorDescription,
      varName: "--primary", 
      icon: <Paintbrush className="h-5 w-5 text-primary shrink-0" /> 
    },
    { 
      label: t.secondaryColorLabel, 
      description: t.secondaryColorDescription,
      varName: "--secondary", 
      icon: <Paintbrush className="h-5 w-5 text-secondary shrink-0" /> 
    },
    { 
      label: t.accentColorLabel, 
      description: t.accentColorDescription,
      varName: "--accent", 
      icon: <Paintbrush className="h-5 w-5 text-accent shrink-0" /> 
    },
  ];


  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-lg md:max-w-xl lg:max-w-2xl max-h-[85vh] flex flex-col">
        <DialogHeader className="pb-4 border-b">
          <DialogTitle className="text-2xl text-primary flex items-center gap-2">
            <Palette className="h-6 w-6" />
            {t.title}
          </DialogTitle>
        </DialogHeader>
        
        <div className="py-6 overflow-y-auto space-y-8 flex-grow pr-2 custom-scrollbar"> {/* Added custom-scrollbar if needed */}
          <section aria-labelledby="palette-title">
            <h3 id="palette-title" className="text-xl font-semibold text-foreground mb-3 flex items-center gap-2">
              <Paintbrush className="h-5 w-5 text-primary"/>
              {t.paletteTitle}
            </h3>
            <p className="text-sm text-muted-foreground mb-4">{t.paletteDescription1}</p>
            <div className="space-y-3">
              {colorInfo.map(color => (
                <div key={color.varName} className="flex items-start gap-3 p-3 bg-card/50 rounded-md border border-border/50">
                  <div className="flex-shrink-0 pt-0.5">{color.icon}</div>
                  <div>
                    <h4 className="font-medium text-card-foreground">{color.label}</h4>
                    <p className="text-xs text-muted-foreground">{color.description} (CSS: <code>hsl(var({color.varName}))</code>)</p>
                  </div>
                </div>
              ))}
            </div>
            <p className="text-sm text-muted-foreground mt-4">{t.paletteDescription2}</p>
          </section>

          <section aria-labelledby="spiral-title">
            <h3 id="spiral-title" className="text-xl font-semibold text-foreground mb-3 flex items-center gap-2">
              <Sprout className="h-5 w-5 text-primary"/>
              {t.spiralTitle}
            </h3>
            <p className="text-sm text-muted-foreground mb-2">{t.spiralDescription1}</p>
            <p className="text-sm text-muted-foreground">{t.spiralDescription2}</p>
          </section>
        </div>

        <div className="pt-4 border-t mt-auto">
          <Button onClick={onClose} variant="outline" className="w-full sm:w-auto">
            {t.closeButton}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
